<template>
	<div>
		<div id="boxWarp">
			<div class="swiper-container">
		    <div class="swiper-wrapper">
		        <div class="swiper-slide" v-for="item in news">
		        	<img :src="item.thumb" class="tab"/>
		        </div>
		        
		    </div>
		    <!-- 如果需要分页器 -->
		    <div class="swiper-pagination"></div>
		    
		   
			</div>
		</div>
		<ul>
			<li v-for="item in news">{{item.mtime|timeFilter}}</li>
		</ul>
		<ul   v-infinite-scroll="loadMore"
			  infinite-scroll-disabled="loading" 
			  infinite-scroll-distance="10">
			<li v-for="item in demo.list" class="clear">
			
					<!--jsonp请求-->
				<!--<div>
					<h4>{{item.title}}</h4>
					<p><span>{{item.source}}</span>
						<span>{{item.ptime}}</span>
					</p>
				</div>
				
				<img :src="item.picInfo[0].url" alt=""  class="new"/>-->
				<!--axios请求-->
				<div>
					<h4>{{item.companyFullName}}</h4>
					<p><span>{{item.city}}</span>
						<span>{{item.createTime}}</span>
					</p>
				</div>
				
				<img :src="'http://www.lgstatic.com/'+item.companyLogo" alt=""  class="new"/>
				
			</li>
		</ul>
		
	</div>
</template>

<script>
	import axios from "axios";
	import Swiper from 'swiper';
	
	import { Toast } from 'mint-ui';
	import "../../../node_modules/swiper/dist/css/swiper.css"
	import {mapActions,mapState} from "vuex"
	export default{
		data(){
			return {
				news:[],
				/*start : 0,
				end:9,
				arr:['A','B01','B02','B10'],
				page :0,*/	
			}
		},
		created(){
			
			
			
			axios.get("/wz/api/v3/get?_=1543633906913").then((res)=>{
				/*console.log(res.data)*/
					this.news=res.data.data.filter((item,index)=>{
						console.log(res.data)
						return index<5
						
					})
				})
			/*this.$jsonp("http://temp.163.com/special/00804KV1/post1603_api_all.js?_=1543632709831",{
   	  		 	callbackName:'callback'
   	  		}).then((res)=>{
   	  		 	this.list=res.data.filter((item,index)=>{	
   	  		 		return index<15&&item.pic[0]
   	  		 	})
   	  		 	
   	  		 })*/
			this.getData();
		
	},
	computed:{
		...mapState(["demo","list"])
		
	},
	methods:{
		loadMore(){//
			
			this.getData()
		},
		...mapActions(["getData"])
		/*,
		getData(){
			if(this.page==6){
				Toast({
						  message: '我是有底线的',
						  position: 'bottom',
						  duration: 1500
						});
						return 
			}
			this.loading=true;//loading为true的时候不允许请求其他数据
			var t=Toast({
						  iconClass:"fa fa-spinner fa-pulse",
						  position: 'bottom',
						  duration: -1
						});*/
			/*this.$jsonp(`http:/
				/3g.163.com/touch/jsonp/sy/recommend/${this.start}-${this.end}.html`,{
				cmiss:'00',

                 refresh:this.arr[this.page]
			}).then((res)=>{
				
				this.list=this.list.concat(res.list.filter((item)=>{
					console.log(item)
					return item.picInfo[0]
				}))
				this.loading=false;
				this.page++;
				this.start=this.start+10;
				this.end=this.start+9;
				t.close()
			})*/
			/*axios.get(`/lg/listmore.json`,{
				params:{
					pagNo:this.page,
					pageSize :15
				}
			}).then((res)=>{
				console.log(res.data.content.data.page.result)
				this.list=this.list.concat(res.data.content.data.page.result);
				this.page++;
				this.loading=false;
				t.close();
			})
		}*/
	},
		watch:{
				news(){
				this.$nextTick(()=>{
					  var mySwiper = new Swiper ('#boxWarp .swiper-container', {
				    
						    loop: true, // 循环模式选项
						    
						    // 如果需要分页器
						    pagination: {
						      el: '.swiper-pagination',
						    },
						    autoplay: {
								    delay: 3000,
								    disableOnInteraction: false //false 用户操作后还能自动播放
								    }
					}) 
				})
			}
		}
	}
</script>

<style lang="scss">
	@import url("../../style/reset.css");
	.tab{
		width: 100%;
		height: (175/25)+rem;
	}
	.clear{
		width: 100%;
		height: 82.64/25+rem;
		box-sizing: border-box;
		padding:10/25+rem;
		div{
			width:217.5/25+rem;
			height: 82.64/25+rem;
			float: left;
		}
	}
	
	h4{
		font-size:0.56rem;
		font-weight: normal;
		margin-bottom: 10/25+rem;
	}
	p{
		
		display: flex;
		justify-content: space-between;
	}
	span{
		
		font-size: 12px;
		color: #2C3E50;
		
	}
	.new{
		width: 110/25+rem;
		height: 69.14/25+rem;
		float: right;
	}
	a{
		color:#000
	}
	.active{
		color: orange;
		border-bottom: 2px solid orange;
	}
</style>


