<template>
	<div >
		<ul    ref="list" v-scroll="vis" dis="100">
			<li v-for="item in list" class="clear">
				<a :href="item.curl">
				<div>
					<h4>{{item.mintro}}</h4>
					<p><span>{{item.source}}</span>
						<span>{{item.ptime}}</span>
					</p>
				</div>
				
				<img :src="item.thumb" alt=""  class="new" />
				</a>
			</li>
			<button class="btn" v-show="vis.flag" v-top>回到顶部</button>
		</ul>
		
	</div>
</template>

<script>
	
	export default{
		data(){
			return {
				list :[],
				type :'',
				vis :{
					flag:false
				}
			}
		},
		watch:{
			$route:{
				handler(n){
	   	  			this.type=n.params.type;
	   	  			console.log(this.type)
	   	  			this.getData();
	   	  	},
	   	  	immediate:true 
			}
			
		},
		methods:{
			getData(){
				this.$jsonp(`http:/
					/cre.mix.sina.com.cn/api/v3/get?cateid=1&cre=tianyi&mod=${this.type}&merge=3&statics=1&length=20&up=2&down=0&fields=media&_=1543822432687`).then((res)=>{
					console.log(res)
					this.list=res.data.filter((item,index)=>{
						return index<15&&item.mintro
					})
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	ul{
		position: fixed;
		top:75/25+rem;
		left:0;
		right:0;
		bottom:50/25+rem;
		overflow: scroll;
	}
	button{
		width: 50/25+rem;
		height: 50/25+rem;
		border-radius: 50%;
		position: fixed;
		bottom:70/25+rem;
		right: 50/25+rem;
		z-index: 999;
		
	}
</style>


