import Vue from "vue"

Vue.filter("timeFilter",(val)=>{
	var d=new Date(val);
	return (d.getMonth()+1)+"月"+d.getDate()+"日  "+d.getHours().toString()
	.padStart(2,"0")+":"+d.getMinutes().toString()
	.padStart(2,"0")+":"+d.getSeconds().toString()
	.padStart(2,"0")
})



