/*import { InfiniteScroll } from 'mint-ui';*/
import { Toast } from 'mint-ui';
import axios from "axios";
export default{
	getData(context){
			if(context.state.page==6){
				Toast({
						  message: '我是有底线的',
						  position: 'bottom',
						  duration: 1500
						});
						return 
			}
			context.commit("changeLoading",true)//loading为true的时候不允许请求其他数据
			var t=Toast({
						  iconClass:"fa fa-spinner fa-pulse",
						  position: 'bottom',
						  duration: -1
						});
			
			axios.get(`/lg/listmore.json`,{
				params:{
					pagNo:context.state.page,
					pageSize :15
				}
			}).then((res)=>{
				console.log(res.data.content.data.page.result)
				context.commit("moreList",res.data.content.data.page.result);
				context.commit("changePage")
				context.commit("changeLoading",false)
				t.close();
			})
		}
	
}



