<template>
	<div>
		<header>
	
			<router-link to="/home" class="back"><i class="fa fa-chevron-left"></i></router-link>
			<div id="box"><input type="text" placeholder="搜索" /></div>
			<a href="#">取消</a>
		</header>
		<mt-button type="default">default</mt-button>
			<mt-button type="primary">primary</mt-button>
			<mt-button type="danger" @click="k">danger</mt-button>
	</div>
</template>

<script>
	import "../../style/reset.css"
	import { Toast } from 'mint-ui';
	export default{
		methods:{
			k(){
				Toast({
				  message: '提示',
				  position: 'middle',
				  duration: 2000
				});
				
			}
			
		}
	}
</script>

<style lang="scss" scoped>
	
	header {
		display: flex;
		width: 100%;
		height: (44/25)+rem;
		
		justify-content: space-between;
		align-items: center;
		.back{
			display: inline-block;
			width: (45/25)+rem;
			height:(45/25)+rem;
			font-size: 20px;
			font-weight: normal;
			text-align: center;
			line-height:(45/25)+rem;
			border-radius: (20/25)+rem;
			
		}
		
		
	}
	
	#box{
			width: (255/25)+rem;
			height: (25/25)+rem;
			background: #008000;
			border-radius: (10/25)+rem;
			
		}
	input{
				width: 100%;
				height: 100%;
				border: none;
				border-radius: (10/25)+rem;
			}
</style>


