<template>
	<div id="box">
		<header>
			<router-link to="/user">返回</router-link>
			<router-link to="/register">注册</router-link>
		</header>
		
		<div id="box1">
			<input type="text" v-model="name"  placeholder="用户名"/>
			<input type="password" v-model="pwd" placeholder="密码"/>
		</div>
		<button @click="login">登录</button>
	</div>
</template>

<script>
	export default{
		data(){
			return {
				name :'',
				pwd : "",
				list :[]
			}
		},
		methods :{
			login(){
				this.$http.get("http://localhost:3000/list").then((res)=>{
					this.list= res.data.filter((item)=>{
					 	return item.name===this.name
					 })
					
					if(this.list==""){
						alert("该用户不存在,请先注册")
						this.$router.push("/register")
						this.name="";
						this.pwd=""
						return
					}else if(this.pwd==''||this.name==""){
						alert("用户名密和码不能为空")
						return
					}
					else if(this.list[0].pwd===this.pwd){
						alert("登录成功!");
						this.name="";
						this.pwd=""
					}else if(this.list[0].pwd!=this.pwd){
						alert("密码不正确");
						this.name="";
						this.pwd=""
					}
				})
				
			}
		}
	}
	
</script>

<style lang="scss" scoped>
	#box{
		background: #f4f4f4;
		header{
			
			width: 100%;
			height: 43.98/25+rem;
			background: #fff;
			display: flex;
			justify-content: space-between;
			font-size:15px;	
			margin-bottom: 50/25+rem;
		}
		a{
			color: #2C3E50;
			margin-top: 10/25+rem;
		}
		#box1{
			
			input{
				display: block;
				width: 100%;
				height: 30px;
				margin-top: 20px;
				
			}
		}
		button{
			width: 100%;
			height: 30/25+rem;
			margin-top: 20/25+rem;
		}
	}
	
</style>


