<template>
	<div>
		<router-link to="/login">返回
		</router-link>
		<div id="box">
			<input type="text" placeholder="请输入手机号" v-model="name" @blur="Name"/>
			<p @click="change" class="change">{{n}}</p>
			<!--<input type="text" v-model="n" @click="change" class="change" placeholder="获取验证码"/>-->
			<input type="text" placeholder="请输入验证码" @blur="yz" v-model="yzs"/>
			<input type="password" placeholder="6-12位数字，字母，下划线" v-model="pwd" @blur="Pwd"/>
			<input type="password" placeholder="确认密码" @blur="TruePwd" v-model="truepwd"/>
		</div>
		<div id="reg">
			<button @click="reg" >完成</button>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return {
				name : "",
				pwd : "",
				truepwd: "",
				list :[],
				n:"获取验证码",
				yzs:""
				
			}
		},
		methods:{
			Name(){
			   var myreg=/^[1][3,4,5,7,8][0-9]{9}$/;
	            if (!myreg.test(this.name)) {
	                alert("手机号格式不正确")
					return  
	            
	            } 
	              
			},
			change(){
				var arr=[];
				for(var i=0;i<4;i++){
					arr.push( Math.floor(Math.random()*10))
				}
				this.n=arr.join("")
				console.log(this.n)
			},
			yz(){
				if(this.yzs!=this.n){
					alert("验证码不正确")
					return 
				}
			},
			Pwd(){
				var reg = /^[\w]{6,12}$/
				 if (!reg.test(this.pwd)) {
	                alert("密码格式不正确")
	                return 
	            } 
	            
			},
			TruePwd(){
				if(this.truepwd!=this.pwd){
					alert("前后密码不一致")
					return 
				}
			},
			reg(){
				if(this.name==""||this.pwd==""||this.truepwd==""){
					alert("请完善信息")
				}
				else{
					this.$http.get("http://localhost:3000/list").then((res)=>{
						this.list= res.data.filter((item)=>{
						 	return item.name===this.name
						 })
						 if(this.list==''){
							this.$http.post("http://localhost:3000/list",{
								name:this.name,
								pwd:this.pwd
							},{
								headers:{
									"content-type":"application/json"
								}
							}).then(()=>{
								alert("注册成功！")
								this.$router.push("/login")
							})
						}else if(this.list[0].name===this.name){
							alert("用户已存在")
						}
						
					})
					
						
					
				}
				
			
			}
		}
	}
</script>

<style>
	.change{
		width: 90px;
		height: 45px;
		background: #CCCCCC;
		border:none;
		color: #00B7FF;
	}
	input{
		display: block;
		margin-top:20px ;
	}
	button{
		width: 100%;
		height: 30px;
		margin-top: 20px;
	}
</style>


