<template>
	<div>
		<header>
			<div class="swiper-container">
		    <div class="swiper-wrapper">
		        <div class="swiper-slide" v-for="(item,index) in tab">
		        	<li :class="{active : index===curIndex}">{{item}}</li>
		        </div>
		    </div>   
		    </div>
		    
		</header>
		<router-view/>
		
		
	</div>
</template>

<script>
	import axios from "axios";
	import Swiper from 'swiper';
	import "../../../node_modules/swiper/dist/css/swiper.css"
	export default {
		data(){
			return {
				
				type:[],
				tab:[],
				curIndex:0
			}
		},
		created(){
			this.tab=this.$store.state.demo.arr.map((item)=>{
				return item.name
			});
			this.type=this.$store.state.demo.arr.map((item)=>{
				return item.type
			});
		},
	    watch:{
		
			tab(){
				
				this.$nextTick(()=>{
					var vm=this;
					var mySwiper = new Swiper('header .swiper-container',{
						  freeMode:true,
						  slidesPerView:'auto',
						  on:{
						  	click(){
						  		vm.curIndex=this.clickedIndex;//es6的方法，this.chickedIndex当前点击的数据的下标
						  		console.log(this)//会有很多方法，wrapperEl是包裹他的容器wrapperEl.clientWidth是容器的宽度
						  		this.setTransition(200);
						  		var el=this.slides[this.clickedIndex];
						  		var elpos=el.offsetLeft+el.clientWidth/2;
						  		var swiperContainerWidth=this.wrapperEl.clientWidth;
						  		var maxDis=this.maxTranslate();//最大距离
						  		var maxPos=-maxDis+swiperContainerWidth/2;
						  		if(elpos<swiperContainerWidth/2){
						  			this.setTranslate(0)
						  		}else if(elpos>maxPos){
						  			this.setTranslate(maxDis)
						  		}else{
						  			this.setTranslate(swiperContainerWidth/2-elpos)
						  		}
						  		if(vm.curIndex===0){
				                    vm.$router.push({'name':'list'})
				                  }
				                  else{
				                     vm.$router.push('/home/index/'+vm.type[vm.curIndex])
				                  }
						  	}
						  }
						//slidesPerView : 'auto',
						//slidesPerView : 3.7,
						})
				})
			}
			
	}
}
</script>

<style lang="scss" scoped>
	@import url("../../style/reset.css");
	header{
		margin-top: 45/25+rem;
		font-size: 15px;
		.swiper-container{
			width:90%;
			height:48.5/25+rem;
			.swiper-slide{
				height: 48.5/25+rem;
				width: 37.5/25+rem;
				padding: 0 5/25+rem;
				li{
					font-size:0.56rem;
					text-align: center;
				}
			}
		}
	}
	
	
</style>


